import { motion } from "framer-motion"
import MKBox from "components/MKBox";
import DefaultCounterCard from "components/Cards/CounterCards/DefaultCounterCard";
import { useInView } from "react-intersection-observer"
import { useEffect } from "react";
import { useAnimation } from "framer-motion";


function Counter() {

  const animation = useAnimation()
  const { ref, inView } = useInView()

  useEffect(() => {
    if (inView) {
      animation.start({
        y: 0,
        transition: { type: "spring", duration: 1, bounce: 0.03 }
      })
      console.log("in view s3")
    }
    if (!inView) {
      console.log("not in view s3")
      animation.start({
        y: +100
      })
    }
  }, [inView])
  return (
    <div ref={ref}>
      <MKBox component="section" py={3}>
        <Container>
          <Grid container item xs={12} lg={9} sx={{ mx: "auto" }}>
            <Grid item xs={12} md={4}>
              <DefaultCounterCard
                count={250}
                suffix="+"
                title="Members enrolled"
                description="As of now, we have 70+ registered members"
              />
            </Grid>
            <Grid item xs={12} md={4} display="flex">
              <Divider orientation="vertical" sx={{ display: { xs: "none", md: "block" }, mx: 0 }} />
              <DefaultCounterCard
                count={10}
                suffix="+"
                title="Initiatives"
                description="We have sucessfully completed 10+ initiatives within the last two years"
              />
              <Divider orientation="vertical" sx={{ display: { xs: "none", md: "block" }, ml: 0 }} />
            </Grid>
            <Grid item xs={12} md={4}>
              <DefaultCounterCard
                count={5}
                title="Global Footprint"
                description="We have our members working in 5+ countries"
              />
            </Grid>
          </Grid>
        </Container>
      </MKBox>
    </div>
  );
}

export default Counter;