function App() {
  return (
    <ThemeProvider theme={theme} >
      <BrowserRouter>
        <Helmet>
          <meta charSet="utf-8" />
          <title>Fosmo</title>
          <meta name="description" content="Alumni of wmo" />
          <link rel="canonical" href="http://wmo.org" />
        </Helmet>
        <CssBaseline />
        {/* <Navbar /> */}
        <DefaultNavbar
          routes={routes}
          sticky
        />
        <Routes>
          <Route path="/" element={<Home />} />
          {/* <Route path="privacy-policy" element={<Privacy />} /> */}
        </Routes>
        <MKBox pt={6} px={1} mt={6}>
          <DefaultFooter content={footerRoutes} />
        </MKBox>
        {/* <Footer /> */}
      </BrowserRouter>
    </ThemeProvider>
  );
}

export default App;