import { useEffect, useRef } from "react";
import { useAnimation } from "framer-motion";
import { useInView } from "framer-motion"



function SectionOne() {

  const animation = useAnimation()
  const ref = useRef(null)
  const isInView = useInView(ref)

  useEffect(() => {
    if (isInView) {
      animation.start({
        y: 0,
        transition: { type: "spring", duration: 3, bounce: 0.03 }
      })
      console.log("in view s1")
    }
    if (!isInView) {
      console.log("not in view s1")
      animation.start({
        y: +100
      })
    }

  }, [isInView])

  return (
    <div >
      <motion.div
        animate={animation}
        ref={ref}
      >
        <MKBox
          display="flex"
          alignItems="center"
          borderRadius="xl"
          my={2}
          py={6}
          sx={{
            backgroundImage: ({ functions: { linearGradient, rgba }, palette: { gradients } }) =>
              `${linearGradient(
                rgba(gradients.dark.main, 0.8),
                rgba(gradients.dark.state, 0.8)
              )}, url(${bgImage})`,
            backgroundSize: "cover",
            backgroundPosition: "center",
          }}
        >
          <Container
          >
            <Grid container item xs={12} lg={6} sx={{ ml: { xs: 0, lg: 6 } }}>
              <MKTypography variant="h4" color="white" fontWeight="bold">
                Alumi Association of Muslim Orphanage, Muttil - Wayana
              </MKTypography>
              <MKTypography variant="h1" color="white" mb={1}>
                Fosmo
              </MKTypography>
              <MKTypography variant="body1" color="white" opacity={0.8} mb={2}>
                Fosmo is a togetherness of former studetns of the orphanage who want to utilize their skill and growth towards the those who are  in need.
              </MKTypography>
              <MKTypography
                component="a"
                // href="https://www.creative-tim.com/learning-lab/react/overview/material-kit/"
                target="_blank"
                rel="noreferrer"
                variant="body2"
                color="white"
                fontWeight="regular"
                sx={{
                  display: "flex",
                  alignItems: "center",

                  "& .material-icons-round": {
                    fontSize: "1.125rem",
                    transform: `translateX(3px)`,
                    transition: "transform 0.2s cubic-bezier(0.34, 1.61, 0.7, 1.3)",
                  },

                  "&:hover .material-icons-round, &:focus .material-icons-round": {
                    transform: `translateX(6px)`,
                  },
                }}
              >
                <Icon sx={{ fontWeight: "bold" }}>arrow_forward</Icon>
              </MKTypography>
            </Grid>
          </Container>
        </MKBox>
      </motion.div>
    </div>
  );
}

